@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  --main-color: #7d7abc;
  --secondary-color: #6457a6;
  --special-color: #ffe347;
  font-family: "Lato";
}

.introduction {
  height: 100vh;
  padding: 0 10vw;
  background-color: var(--main-color);
  color: var(--special-color);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-size: x-large;
}

.introduction > div {
  width: 50vw;
}

.about {
  height: 50vh;
  padding: 5vh 10vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: var(--secondary-color);
  color: white;
  font-size: x-large;
}

.previous-projects {
  background-color: var(--main-color);
  padding: 5vh 10vw;
  font-size: x-large;
  color: white;
}

.previous-projects > div:nth-child(odd) > div {
  flex-direction: row-reverse;
}

.project {
  margin-top: 3vh;
  background-color: var(--secondary-color);
  padding: 5vh 10vw;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 5vh;
}

.project > div {
  display: flex;
  flex-direction: row;
  gap: 5vw;
}

.project img {
  height: 30vh;
  width: auto;
}

.project > div > div:nth-child(1) {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.project > div > div:nth-child(2) {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.project > div > div > div {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.project button {
  height: 3rem;
  width: 7rem;
  background-color: var(--special-color);
  border: none;
}

.contact {
  background-color: var(--secondary-color);
  padding: 5vh 10vw;
  color: white;
  font-size: x-large;
}

.contact ul {
  list-style: none;
}

.contact a {
  text-decoration: none;
  color: white;
}

@media screen and (max-width: 600px) {
  .introduction {
    flex-direction: column;
  }

  .project img {
    width: 95%;
    height: auto;
  }

  .project > div {
    flex-direction: column;
  }

  .project > div > div {
    gap: 10px;
  }

  .previous-projects > div:nth-child(odd) > div {
    flex-direction: column;
  }
}
